import React from 'react';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import MainButton from '../components/button/MainButton';
import decore from './../assets/img/decore.svg'

const validateName = (value) => {
   let error;
   if (!value) {
      error = '\* The field is required';
   } else if (!/^[A-Z][a-z]*$/.test(value)) {
      error = '\* The name must consist only of Latin letters, the first letter must be capitalized';
   }
   return error;
};
const validateSureName = (value) => {
   let error;
   if (!value) {
      error = '* The field is required';
   } else if (!/^[A-Z][a-z]*$/.test(value)) {
      error = '\* The surname must consist only of Latin letters, the first letter must be capitalized';
   }
   return error;
};

const validateEmail = (value) => {
   let error;
   if (!value) {
      error = '* The field is required';
   } else if (
      !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(value)
   ) {
      error = '* Incorrect e-mail';
   }
   return error;
};
const FormPageBtn = "SUBSCRIBE";

const FormPage = () => {
   return (

      <section className='Form-page __container' id="contact">
         <div className="Form-page__title" >
            <h2>DONT FORGET TO SIGN UP</h2>
            <p>Find out early about all upcoming promotions and new product releases with
               our newsletter.</p>
         </div>
         <Formik
            initialValues={{ name: '', email: '' }}
            onSubmit={(values) => {
               //     
               localStorage.setItem('formValues', JSON.stringify(values));
            }}
         >
            {({ errors, touched }) => (
               <Form className="Form-page__form">
                  <div className="Form-page__block" >
                     <div>
                        <Field name="name" placeholder="Enter your name..." className="Form-page__name Form-page__input" validate={validateName} />
                        <ErrorMessage name="name" component="div" className="error" />
                     </div>
                     <div>
                        <Field name="sureName" placeholder="Enter your surname....." className="Form-page__sure-name Form-page__input" validate={validateSureName} />
                        <ErrorMessage name="sureName" component="div" className="error" />
                     </div>
                     <div>
                        <Field name="email" placeholder="Enter your e-mail....." className="Form-page__email Form-page__input" validate={validateEmail} />
                        <ErrorMessage name="email" component="div" className="error" />
                     </div>
                  </div>
                  <MainButton type="submit" description={FormPageBtn}></MainButton>
               </Form>
            )}
         </Formik>
         <img src={decore} alt="" />
      </section>
   );
};

export default FormPage;
