import LatestNewsComponent from "../components/LatestNews/LatestNewsComponent";
import LatestNews_img from "./../assets/img/LatestNews/LatestNews_img.png"
import LatestNews_img2 from "./../assets/img/LatestNews/LatestNews_img2.png"
import LatestNews_decore from "./../assets/img/LatestNews/icon/LatestNews_decore.svg"
import React, { useEffect, useState } from 'react';

const LatestNews = () => {

   const LatestNewsData = [
      {
         title: "2020 Interior Design Trends",
         text: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod         incididunt ut labore et dolore magna aliqua.",
      },
      {
         title: "28 Notable Product at ARC Interior Design",
         text: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod incididunt ut labore et dolore magna aliqua.",
      },
   ];

   const [isVisible, setIsVisible] = useState(false);

   useEffect(() => {
      const handleScroll = () => {
         const component = document.querySelector('.component');
         const componentTop = component.getBoundingClientRect().top;
         const windowHeight = window.innerHeight;
         const componentBottom = component.getBoundingClientRect().bottom;
         const isVisible = (componentTop < (windowHeight - 300) && componentTop > 0) || (componentBottom > 0 && componentBottom < (windowHeight - 100));
         setIsVisible(isVisible);
      };
      window.addEventListener('scroll', handleScroll);
      return () => {
         window.removeEventListener('scroll', handleScroll);
      };
   }, []);

   return (
      <section className="LatestNews" id="news">
         <div className={`LatestNews__contein __container component ${isVisible ? 'component--visible' : ''}`}>
            <p>LATEST NEWS</p>
            <h2>From Our Blog</h2>
            <div className="LatestNews__row">
               <LatestNewsComponent
                  img={LatestNews_img}
                  decore={LatestNews_decore}
                  title={LatestNewsData[0].title}
                  text={LatestNewsData[0].text} />
               <LatestNewsComponent
                  img={LatestNews_img2}
                  decore={LatestNews_decore}
                  title={LatestNewsData[1].title}
                  text={LatestNewsData[1].text} />

            </div>

         </div>
      </section>
   )
}

export default LatestNews;