import MainButton from "../button/MainButton";

const LatestNewsComponent = (props) => {
   const LatestNewsBtn = 'CONTINUE READING....'
   return (
      <div className="LatestNews__block">
         <div className="LatestNews__img">
            <img src={props.img} />
            <img className="LatestNews__img-decore" src={props.decore} />
         </div>
         <h3>{props.title}</h3>
         <p>{props.text}</p>
         <MainButton
            description={LatestNewsBtn} />

      </div>
   )
}

export default LatestNewsComponent;