import MainButton from "../components/button/MainButton"
import about_img from "../assets/img/About/about_img.png"
import about_decore from "../assets/img/About/decore/about_decore.svg"


const About = () => {
   const aboutBtn = [
      {
         description: "LEARN MORE"
      }
   ]
   return (
      <section className="about __container" id="about">
         <div className="about__contein">
            <div className="about__img">
               <img src={about_img} />
               <img className="about__img-decore" src={about_decore} />
            </div>
            <div className="about__info">
               <h3>ABOUT US</h3>
               <h2>Interioris The Will
                  of An Epoch Mextreo</h2>
               <p>There are many variations of passages of Lorem Ipsum available,
                  but the majority have suffered alteration in some form injected
                  humour, or randomised words which don't look even slightly
                  believable.If you are going to use a passage of Lorem Ipsum,
                  sure there isn't anything embarrassing hidden the middleof text.
                  All the Lorem Ipsum generators on the Internettend to repeat
                  predefined chunks as necessary,making this the first true
                  generator on the Internet.</p>
               <MainButton description={aboutBtn[0].description} />
            </div>

         </div>
      </section>
   )
}

export default About;