import MainButton from "../components/button/MainButton"
import modern_img from "../assets/img/Modern/modern_img.png"
import modern_decore from "../assets/img/Modern/icon/modern_decore.svg"
import decore from './../assets/img/decore.svg'


const Modern = () => {
   const ModernBtn = [
      {
         description: "Contact"
      }
   ]
   return (
      <section className="modern __container">
         <div className="modern__contein">
            <div className="modern__info">
               <h3>MODERN INTERIOR</h3>
               <h1>Create Your
                  Interior Design.</h1>
               <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod
                  tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim
                  veniam, quis nostrud exercitation.</p>
               <MainButton description={ModernBtn[0].description} />
            </div>
            <div className="modern__img">
               <img src={modern_img} />
               <img className="modern__img-decore" src={modern_decore} />
            </div>
         </div>
         <img src={decore} alt="decore_icon" />
      </section>
   )
}

export default Modern;