import CompletedWorkComponent from "../components/CompletedWorkComponent/CompletedWorkComponent";

import completed_work_icon1 from "./../assets/img/completedWork/completed_work_icon1.svg"
import completed_work_icon2 from "./../assets/img/completedWork/completed_work_icon2.svg"


const CompletedWork = () => {
   const CompletedWorkData = [
      {
         number: "980",
         name: "Project",
      },
      {
         number: "520",
         name: "Happy Client",
      },
      {
         number: "330",
         name: "Winners",
      },
      {
         number: "120",
         name: "Recoment",
      },
   ];

   return (
      <section className="completed-Work">
         <div className="completed-Work__body">
            <div className="completed-Work__title">
               <h2>3000+ Completed Work <br />
                  Which WE have Successfully Done</h2>
            </div>
            <div className="completed-Work__content">
               <div className="completed-Work__data-1">
                  <CompletedWorkComponent
                     number={CompletedWorkData[0].number}
                     name={CompletedWorkData[0].name} />
                  <img src={completed_work_icon1} />
               </div>
               <div className="completed-Work__data-2">
                  <CompletedWorkComponent
                     number={CompletedWorkData[1].number}
                     name={CompletedWorkData[1].name} />
                  <img src={completed_work_icon2} />
               </div>
               <div className="completed-Work__data-3">
                  <CompletedWorkComponent
                     number={CompletedWorkData[2].number}
                     name={CompletedWorkData[2].name} />
                  <img src={completed_work_icon1} />
               </div>
               <div className="completed-Work__data-4">
                  <CompletedWorkComponent
                     number={CompletedWorkData[3].number}
                     name={CompletedWorkData[3].name} />
               </div>
            </div>
         </div>
      </section>
   )
}

export default CompletedWork;

