import header_logo from '../../src/assets/img/header/header_logo.svg'
import MainButton from '../components/button/MainButton';
import React, { useEffect, useState } from 'react';
import Menu from '../components/Menu/Menu';
import { Link, animateScroll as scroll } from "react-scroll";

const Header = () => {
   const headerBtn = [
      {
         description: "Sign up"
      },
   ];
   const items = [
      {
         value: "HOME",
         href: '/HOME'
      },
      {
         value: "PROJECT",
         href: '/PROJECT'
      },
      {
         value: "SERVICES",
         href: '/SERVICES'
      },
      {
         value: "ABOUT",
         href: '/ABOUT'
      },
      {
         value: "BLOG",
         href: '/BLOG'
      },
      {
         value: "SHOP",
         href: '/SHOP'
      },
      {
         value: "CONTACT",
         href: '/CONTACT'
      },

   ]
   const [menuActive, setMenuActive] = useState(false);

   return (
      <header className="header">
         <div className="header__contein __container">
            <a className="header__logo" href="#"><img src={header_logo} alt="Help_image" /></a>
            <div className="header__menu">
               <div className="menu__body">
                  <ul className="menu__list">
                     <li className="menu__item">
                        <a className="menu__link" href="#"><div>HOME</div></a>
                     </li>
                     <li className="menu__item">
                        <a className="menu__link" href="#"><div>PROJECT</div></a>
                     </li>
                     <li className="menu__item">
                        <a className="menu__link" href="#"><div>SERVICES</div></a>
                     </li>
                     <li className="menu__item">
                        <Link
                           to="about"
                           smooth={true}
                           duration={500}
                        >
                           <a className="menu__link" href="#about"><div>ABOUT</div></a></Link>

                     </li>
                     <li className="menu__item">
                        <Link
                           to="blog"
                           smooth={true}
                           duration={500}>
                           <a className="menu__link" href="#"><div>BLOG</div></a>
                        </Link>

                     </li>
                     <li className="menu__item">
                        <Link
                           to="news"
                           smooth={true}
                           duration={500}>
                           <a className="menu__link" href="#"><div>news</div></a>
                        </Link>

                     </li>
                     <li className="menu__item">
                        <Link
                           to="contact"
                           smooth={true}
                           duration={1000}>
                           <a className="menu__link" href="#"><div>CONTACT</div></a>
                        </Link>

                     </li>
                     <MainButton
                        description={headerBtn[0].description} />
                  </ul>
               </div >

               <button className="menu_icon icon-menu" type="button">
                  <span className="icon-menu__line"></span>
               </button>
            </div>

            <nav>
               <div className="burger-btn" onClick={() => setMenuActive(!menuActive)}>
                  <span></span>
               </div>
               <a className="burger__logo" href="#"><img src={header_logo} alt="burger_logo" /></a>
            </nav>
            <Menu active={menuActive} setActive={setMenuActive} items={items} />
         </div>
      </header>
   )
}

export default Header;