import React from 'react';
import { useState } from 'react';
import Testimonials_img1 from './../assets/img/Testimonials/testimonials_img1.png';
import Testimonials_img2 from './../assets/img/Testimonials/testimonials_img2.png';
import Testimonials_decore from './../assets/img/Testimonials/testimonials_decore.svg';
import testimonials_arrow_icon from './../assets/img/Testimonials/testimonials_arrow_icon.svg';


const Testimonials = () => {


   const testimonialsData = [
      {
         id: 1,
         name: 'Louis Saville',
         position: 'CEO at Google inc',
         text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.',
         image: Testimonials_img1,
         decore: Testimonials_decore
      },
      {
         id: 2,
         name: 'John Doe',
         position: 'Marketing Manager at ABC company',
         text: 'Suspendisse potenti. Suspendisse ac fermentum est. Nulla facilisi. Donec vel lorem at augue suscipit lobortis.',
         image: Testimonials_img2,
         decore: Testimonials_decore
      },
      {
         id: 3,
         name: 'Louis Saville',
         position: 'CEO at Google inc',
         text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.',
         image: Testimonials_img1,
         decore: Testimonials_decore
      },
      {
         id: 4,
         name: 'John Doe',
         position: 'Marketing Manager at ABC company',
         text: 'Suspendisse potenti. Suspendisse ac fermentum est. Nulla facilisi. Donec vel lorem at augue suscipit lobortis.',
         image: Testimonials_img2,
         decore: Testimonials_decore
      },
   ];



   const [currentSlide, setCurrentSlide] = useState(0);

   const handlePrevClick = () => {
      if (currentSlide === 0) {
         setCurrentSlide(testimonialsData.length - 2);
      } else {
         setCurrentSlide((prevSlide) => prevSlide - 1);
      }
   };

   const handleNextClick = () => {
      if (currentSlide >= testimonialsData.length - 2) {
         setCurrentSlide(0);
      } else {
         setCurrentSlide((prevSlide) => prevSlide + 1);
      }
   };

   return (
      <section className="testimonials __container" id="blog">

         <div className="testimonials__body">
            <div className="testimonials__title">
               <p>TESTIMONIALS</p>
               <h2>Client says about us</h2>
            </div>
            <div className="testimonials__slider">
               {testimonialsData.slice(currentSlide, currentSlide + 2).map((testimonial) => (
                  <div className="testimonials__slide" key={testimonial.id}>
                     <div className="testimonials__slide-list-1">
                        <img className="testimonials__img" src={testimonial.image} alt="testimonials_img" />
                        <img className="testimonials__decore" src={testimonial.decore} alt="testimonials_decore" />
                        <div className="testimonials__slide-item-1">
                           <p>{testimonial.name}</p>
                           <p>{testimonial.position}</p>
                        </div>
                        <div className="testimonials__slide-item-2">
                           <p>{testimonial.text}</p>
                        </div>
                     </div>
                  </div>
               ))}
            </div>
            <div className="testimonials__slider-controls">
               <button className="testimonials__slider-control testimonials__slider-control--prev" onClick={handlePrevClick}>
                  <img src={testimonials_arrow_icon} alt="testimonials_arrow_icon" />
               </button>
               <button className="testimonials__slider-control testimonials__slider-control--next" onClick={handleNextClick} >
                  <img src={testimonials_arrow_icon} alt="testimonials_arrow_icon" />
               </button>
            </div>
         </div>
      </section>
   );
}

export default Testimonials;