

const CompletedWorkComponent = (props) => {
   return (
      <div className="completed-work__contein">
         <div className="completed-work__block">
            <div className="completed-work__info">
               {props.number}
            </div>
            <p>{props.name}</p>
         </div>
      </div>
   )
}

export default CompletedWorkComponent;