
import MainButton from "../components/button/MainButton"
import SuccessfulWork_img from "../assets/img/SuccessfulWork/SuccessfulWork_img.png"
import SuccessfulWork_decore from "../assets/img/SuccessfulWork/decore/SuccessfulWork_decore.svg"
import SuccessfulWork_info_decore from "../assets/img/SuccessfulWork/decore/SuccessfulWork_info_decore.svg"
import decore from './../assets/img/decore.svg'


const SuccessfulWork = () => {
   const SuccessfulWorkBtn = [
      {
         description: "READ MORE"
      }
   ]
   return (
      <section className="SuccessfulWork __container">
         <div className="SuccessfulWork__contein">
            <div className="SuccessfulWork__info">
               <img src={SuccessfulWork_info_decore} />
               <h2>Years Of Successful Working <br /> The Market</h2>
               <MainButton description={SuccessfulWorkBtn[0].description} />
            </div>
            <div className="SuccessfulWork__img">
               <img src={SuccessfulWork_img} />
               <img className="SuccessfulWork__img-decore" src={SuccessfulWork_decore} />
            </div>
            <img src={decore} alt="decore_icon" />
         </div>
      </section>
   )
}

export default SuccessfulWork;